import {FunctionComponent, useState} from "react";
import {StyledContainer} from "../TestComponent/TestComponent.styles";
import {Formik, Field, Form} from "formik";
import {TwoPointCenterWallHangValues} from "./WoodFormValues";
import {TextField} from "formik-mui";
import {Button, Typography} from "@material-ui/core";
import Fraction from "fraction.js";

const WoodComponent: FunctionComponent = () => {

    const [middlePoint, setMiddlePoint] = useState<string>('');
    const [outerLength, setOuterLength] = useState<string>('');

    const initialValues: TwoPointCenterWallHangValues = {totalLength: '', lengthBetweenPoints: ''};
    let submittedValues: TwoPointCenterWallHangValues | undefined = undefined;

    const getDecimal = (fraction: string): number => {
        const splitStr = fraction.split(" ");
        let decimal: number = 0;

        if (splitStr.length === 2) {
            const splitFraction = splitStr[1].split("/");
            const numerator: number = +splitFraction[0];
            const denominator: number = +splitFraction[1];
            decimal = numerator / denominator;
        }

        const wholeNumber: number = +splitStr[0];
        return decimal + wholeNumber;
    };

    const getFraction = (decimal: number): string => {
        const fraction = new Fraction(decimal);
        return fraction.toFraction(true);
    };

    const validate = (values: TwoPointCenterWallHangValues) => {
        const errors: any = {};

        if (!values.totalLength) {
            errors.totalLength = 'Required';
        } else if (!/\d+( \d+\/\d+)*$/i.test(values.totalLength)) {
            errors.totalLength = 'Invalid length value';
        }

        if (!values.lengthBetweenPoints) {
            errors.lengthBetweenPoints = 'Required';
        } else if (!/\d+( \d+\/\d+)*$/i.test(values.lengthBetweenPoints)) {
            errors.lengthBetweenPoints = 'Invalid length value';
        }
        return errors;
    };

    const submitForm = (values: TwoPointCenterWallHangValues) => {
        const decTotalLength = getDecimal(values.totalLength);
        const decLengthBetweenPoints = getDecimal(values.lengthBetweenPoints);

        setMiddlePoint(getFraction(decTotalLength / 2));
        setOuterLength(getFraction((decTotalLength - decLengthBetweenPoints) / 2));

        submittedValues = values;
    };

    return (
        <StyledContainer>
            <Formik initialValues={submittedValues ?? initialValues} validate={validate} onSubmit={(values, { resetForm }) => {
                submitForm(values);
                resetForm();
            }}>
                <Form>
                    <Field
                        component={TextField}
                        name={'totalLength'}
                        label={'Total Length'}
                        type='text'
                    />
                    <Field
                        component={TextField}
                        name={'lengthBetweenPoints'}
                        label={'Length Between Hanging Points'}
                        type='text'
                    />
                    <Button variant={'outlined'} type={'submit'}>Submit</Button>
                </Form>
            </Formik>
            <Typography>Middle Point: {middlePoint}</Typography>
            <Typography>Outer Length: {outerLength}</Typography>
        </StyledContainer>
    )
};

export default WoodComponent;